/* src/components/TimeTable.css */

.time-slot {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  font-size: 1.05rem;
  font-weight: 400;
  color: #333;
  border-bottom: 1px solid #eee;
  background-color: #ffffff;
}

.time-slot:nth-child(even) {
  background-color: #f7f7f7;
}

.time-slot.reserved {
  font-weight: 600;
  color: #d93025;
}

.time-slot:hover {
  background-color: #f0f0f5;
  cursor: default;
}

.slot-time {
  flex: 1;
}

.slot-info {
  flex: 1;
  text-align: right;
}

